@import url(https://fonts.googleapis.com/css2?family=ABeeZee&display=swap);
.crSelPrinc {
  height: 400px;
}

.crSelPoint {
  position: absolute;
  bottom: -25px;
  display: block;
  width: 100%;
  padding: 0;
  margin: 0;
  list-style: none;
  text-align: center;
}

.crSelPoint li {
  position: relative;
  display: inline-block;
  width: 20px;
  height: 20px;
  margin: 0 5px;
  padding: 0;
  cursor: pointer;
}

.crSelPoint li button {
  font-size: 0;
  line-height: 0;
  display: block;
  width: 20px;
  height: 20px;
  padding: 5px;
  cursor: pointer;
  color: transparent;
  border: 0;
  outline: none;
  background: transparent;
}

.crSelPoint li button:hover {
  opacity: 100;
}

.crSelPoint li button:before {
  font-family: "slick";
  font-size: 30px;
  line-height: 20px;
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 20px;
  content: "•";
  text-align: center;
  opacity: 0.15;
  color: #fff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.crSelPoint li.slick-active button:before {
  opacity: 0.75;
  color: #fff;
}

